import collectItem from "../../components/collect/collectItem.vue"
export default {
	name: "",
	components: {
		collectItem,
	},
	props: {},
	data() {
		return {
			collection: null
		}
	},
	methods: {
		goShop() {
			uni.switchTab({
				url: "/pages/index/index"
			})
		},
		// 查看收藏
		getCollect(id) {
			this.$api.getCollect(id).then(res => {
				if (res.status === 200) {
					this.collection = res.data.collectGoodsList
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
	mounted() {

	},
	onLoad() {
		this.getCollect(this.$store.state.openId)
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
